import React from 'react'
import ProjectCard from './ProjectCard'

function Projects() {
  return (
    <section id="projects" className='bg-[#101015] text-gray-400 py-6'>
        <h1 className='text-[1.5rem] tracking-widest uppercase font-thin my-2'>Projets</h1>
        <div className='pb-10 px-2 py-5 md:max-w-3xl lg:max-w-6xl m-auto flex flex-col md:grid md:grid-cols-2 justify-center items-center space-y-7 md:space-y-0 md:gap-y-12'>
          <ProjectCard imgPath="./img/projects/0_svgtsx.png" lang=".NET (6.0)" link='https://github.com/wilfregd/SvgTsxConverter' title='SvxTsxConverter' description='Outil pour convertir des images .svg en objets .tsx prts  importer dans les projets React.' />
          <ProjectCard imgPath="./img/projects/1_booki.png" lang="HTML5, CSS3" link='https://github.com/wilfregd/OpenClassrooms-P2' title='Projet Formation: Booki' description='Site responsive uniquement HTML5 et CSS3' />
          <ProjectCard imgPath="./img/projects/2_ohmyfood.png" lang="HTML5, CSS3" link='https://github.com/wilfregd/OpenClassrooms-P3' title='Projet Formation: Ohmyfood!' description="Site pens pour les appareils mobiles. Prototype d'application pour restaurant." />
          <ProjectCard imgPath="./img/projects/3_kanap.png" lang="HTML5, CSS3, Javascript, Node.js" link='https://github.com/wilfregd/OpenClassrooms-P5' title='Projet Formation: Kanap' description="Prototype de site e-commerce, centr sur l'implmentation du Javascript sur une maquette." />
          <ProjectCard imgPath="./img/projects/4_groupomania.png" lang="Vue.js, Node.js, Express, MySql, Sequelize, HTML5, CSS3" link='https://github.com/wilfregd/OpenClassrooms-P7-Groupomania' title='Projet Formation: Groupomania' description="Projet final de formation. Rseau social interne d'entreprise incluant un framework front-end, une API et prenant en compte la scurit des utilisateurs." />
          <ProjectCard imgPath="./img/projects/5_voxel.jpg" lang="Godot, C#" link='https://starduststudios.itch.io/voxelverse' title='Voxel Engine' description="(Voir la section 'Development Log') Gnration procdurale de mondes 3D type 'Voxel'. " />
          <ProjectCard imgPath="./img/projects/6_misc.png" lang="Unity, Godot, UE4, C#" link='https://www.instagram.com/wilfre_swk/' title='Projets divers' description="Diffrents projets personnels au fil des annes, principalement Godot, Unity et UE4." />
        </div>
        <div className='wave-bottom wave h-40 md:h-[30%] w-full' />
      </section>
  )
}

export default Projects